"use client"

import { Card } from "../../components/ui/card"
import { useEffect, useState } from "react"
import { Button } from "../../components/ui/button"
import { supabase } from "../../lib/supabaseClient"

type FormData = {
  [key: string]: string
}

export default function Records() {
  const [records, setRecords] = useState<FormData[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const fetchRecords = async () => {
      try {
        const { data, error } = await supabase
          .from('form_submissions')
          .select('*')
          .order('created_at', { ascending: false })

        if (error) {
          throw error
        }

        setRecords(data || [])
      } catch (err: any) {
        setError(err.message)
      } finally {
        setLoading(false)
      }
    }

    fetchRecords()
  }, [])

  return (
    <main className="min-h-screen bg-gray-50">
      <div className="bg-black text-white py-16 mb-8">
        <div className="container mx-auto px-4 text-center">
          <h1 className="text-5xl font-bold mb-4">Commonwealth Food Manufacturing</h1>
          <p className="text-xl text-gray-300">All Submitted Forms</p>
        </div>
      </div>

      <div className="container mx-auto px-4 pb-16 max-w-7xl">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-2xl font-semibold">Request Records</h2>
          <Button
            onClick={() => window.location.href = "/"}
            className="bg-black hover:bg-gray-800 text-white"
          >
            Back to Form
          </Button>
        </div>

        {error && (
          <div className="bg-red-50 text-red-700 p-4 rounded-lg mb-6">
            {error}
          </div>
        )}

        {loading ? (
          <div className="text-center py-8">Loading...</div>
        ) : records.length === 0 ? (
          <Card className="p-6">
            <p className="text-center text-gray-500">No records found</p>
          </Card>
        ) : (
          <Card className="p-6 overflow-hidden">
            <div className="overflow-x-auto">
              <table className="min-w-full border border-gray-300">
                <thead>
                  <tr className="bg-gray-100">
                    <th className="border px-4 py-2">Requestor</th>
                    <th className="border px-4 py-2">Customer</th>
                    <th className="border px-4 py-2">Project Name</th>
                    <th className="border px-4 py-2">Product Name</th>
                    <th className="border px-4 py-2">Type</th>
                    <th className="border px-4 py-2">Target BOM Cost</th>
                    <th className="border px-4 py-2">Date Requested</th>
                    <th className="border px-4 py-2">Status</th>
                  </tr>
                </thead>
                <tbody>
                  {records.map((record, index) => (
                    <tr key={index} className="hover:bg-gray-50">
                      <td className="border px-4 py-2">{record.requestor}</td>
                      <td className="border px-4 py-2">{record.customer}</td>
                      <td className="border px-4 py-2">{record.project_name}</td>
                      <td className="border px-4 py-2">{record.product_name}</td>
                      <td className="border px-4 py-2">{record.type}</td>
                      <td className="border px-4 py-2">{record.target_bom_cost}</td>
                      <td className="border px-4 py-2">{record.date_requested}</td>
                      <td className="border px-4 py-2">{record.product_status}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </Card>
        )}
      </div>
    </main>
  )
}
